.fcx-pb-5     { padding-bottom: 0.5vw !important; }
.fcx-pb-10    { padding-bottom: 1vw !important; }
.fcx-pb-15    { padding-bottom: 1.5vw !important; }
.fcx-pb-20    { padding-bottom: 2vw !important; }
.fcx-pb-25    { padding-bottom: 2.5vw !important; }
